.transactions-page {
    background-color: vwhite;
    min-height: 100vh;
    padding: 2rem 6rem;
    box-sizing: border-box;
}
.arrow i {
    font-size: 1rem;
    color: #fff;
    transition: transform 0.3s ease;
}
.account-banner {
    background-color: #333;
    color: white;
    border-radius: 5px;
    padding: 2rem;
    margin-bottom: 2rem;
    display: flex;
    justify-content: space-between;
    align-items: center;

    .account-summary {
        h3 {
            margin: 0;
            font-size: 1.5rem;
        }

        .account-balance {
            font-size: 2rem;
            font-weight: bold;
        }

        .balance-label {
            font-style: italic;
            color: #ddd;
        }
    }

    .close-btn {
        font-size: 1.5rem;
        cursor: pointer;
        color: #aaa;

        &:hover {
            color: white;
        }
    }
}

.transactions-header {
    display: flex;
    justify-content: space-between;
    font-weight: bold;
    padding: 1rem;
    color: white;
    border-bottom: 2px solid #00bc77;
    margin-bottom: 1rem;

    span {
        flex: 1;
        text-align: left;
    }
}